* {
  margin: 0;
}

html {
  --active-color: #00acdc;
  font-family: Roboto, sans-serif;
}

.header {
  display: flexbox;
  background-color: #fff;
  justify-content: space-between;
}

.logo {
  width: 40px;
  height: 40px;
  margin-top: 10px;
  margin-left: 50px;
  display: inline-block;
}

.nav {
  align-items: flex-end;
  height: 60px;
  margin-right: 50px;
  display: inline-block;
}

a.is-active, .highlight {
  color: var(--active-color);
}

.link {
  text-transform: uppercase;
  color: #000;
  align-content: center;
  padding: 0;
  font-size: 12px;
  text-decoration: none;
  list-style: none;
  display: inline-block;
}

.link:hover {
  color: purple;
  text-decoration: underline;
}

.is-active:after {
  background-color: var(--active-color);
  border-radius: 8px;
  width: 37px;
  height: 4px;
}
